import { useContext, useEffect, useState } from "react";
import { UserContext } from "../context/UserContext";
import { useNavigate } from "react-router-dom";
import { apiRequest } from "../api/api";
import { toast } from "react-toastify";
import "./MyPage.css";

const MyPage = () => {
  const { user, setUser } = useContext(UserContext);
  const navigate = useNavigate();

  const [nickname, setNickname] = useState("");
  const [preferred, setPreferred] = useState("");
  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [isPasswordChangeVisible, setIsPasswordChangeVisible] = useState(false);

  //      
  if (!user) {
    toast.error(" .");
    navigate("/login");
    return null;
  }

  //    
  useEffect(() => {
    const fetchUserInfo = async () => {
      try {
        const res = await apiRequest("/api/users/mypage", "GET", null, user.email);
        setNickname(res.data.nickname);
        setPreferred(res.data.preferred_factor);
        setUser(res.data);
      } catch (err) {
        toast.error(" .");
        navigate("/login");
      }
    };
    fetchUserInfo();
  }, []);

  const handleProfileUpdate = async () => {
    try {
      await apiRequest(
        "/api/users/mypage",
        "PATCH",
        { nickname, preferred_factor: preferred },
        user.email
      );

      toast.success(" .");
      setUser((prev) => ({
        ...prev,
        nickname,
        preferred_factor: preferred,
      }));
    } catch (error) {
      toast.error(error.message || " ");
    }
  };

  const handlePasswordChange = async () => {
    if (newPassword !== confirmPassword) {
      toast.error("     .");
      return;
    }

    try {
      await apiRequest(
        "/api/users/password",
        "PATCH",
        { current_password: currentPassword, new_password: newPassword },
        user.email
      );

      toast.success(" .");
      setCurrentPassword("");
      setNewPassword("");
      setConfirmPassword("");
      setIsPasswordChangeVisible(false);
    } catch (error) {
      toast.error(error.message || "  ");
    }
  };

  //   
  const handleDeleteAccount = async () => {
    try {
      await apiRequest("/api/users", "DELETE", null, user.email);
      toast.info("  .");
      setUser(null);
      document.cookie = "email=; max-age=0; path=/";
      sessionStorage.clear();
      navigate("/login");
    } catch (error) {
      toast.error(error.message || "  ");
    }
  };

  return (
    <div className="mypage-container">
      <h1 className="mypage-title"></h1>
      <form className="mypage-form" onSubmit={(e) => e.preventDefault()}>
        <div className="form-group">
          <label></label>
          <input type="text" value={user?.email || ""} disabled />
        </div>
        <div className="form-group">
          <label></label>
          <input
            type="text"
            value={nickname}
            onChange={(e) => setNickname(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label> </label>
          <select value={preferred} onChange={(e) => setPreferred(e.target.value)}>
            <option value="FEE"></option>
            <option value="DISTANCE"></option>
            <option value="RATING"></option>
            <option value="CONGESTION"></option>
          </select>
        </div>

        <div className="section">
          <h3 className="section-title"> </h3>
          <button
            type="button"
            className="mypage-button activity"
            onClick={() => navigate("/ratings")}
          >
               
          </button>
        </div>

        <button
          type="button"
          className="mypage-button main"
          onClick={handleProfileUpdate}
        >
           
        </button>

        <button
          type="button"
          className="mypage-button toggle"
          onClick={() => setIsPasswordChangeVisible(!isPasswordChangeVisible)}
        >
          {isPasswordChangeVisible ? "  " : " "}
        </button>

        {isPasswordChangeVisible && (
          <div className="password-section">
            <div className="form-group">
              <label> </label>
              <input
                type="password"
                value={currentPassword}
                onChange={(e) => setCurrentPassword(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label> </label>
              <input
                type="password"
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label>  </label>
              <input
                type="password"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
              />
            </div>
            <button
              type="button"
              className="mypage-button"
              onClick={handlePasswordChange}
            >
               
            </button>
          </div>
        )}

        {/*    */}
        <button
          type="button"
          className="mypage-button delete"
          onClick={handleDeleteAccount}
        >
           
        </button>
      </form>
    </div>
  );
};

export default MyPage;
