import React, { useEffect, useState } from 'react';
import './UserDetailModal.css';
import './AdminCommon.css';
import { useNavigate } from 'react-router-dom';
import { apiRequest } from '../../api/api';

export default function UserDetailModal({ user, onClose, onDelete }) {
  const [ratings, setRatings] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchRatings = async () => {
      if (!user) return;
      try {
        const keyword = encodeURIComponent(user.nickname);
        const res = await apiRequest(
          `/admin/api/ratings/search?keyword=${keyword}`,
          'GET'
        );
        setRatings(res.data);
      } catch (err) {
        console.error('  ', err);
      }
    };
    fetchRatings();
  }, [user]);

  const handleViewRatings = () => {
    navigate('/admin/ratings', { state: { presetSearch: user.nickname } });
    onClose();
  };

  const handleDelete = async () => {
    if (window.confirm(` "${user.nickname}"  ?`)) {
      try {
        await apiRequest(`/admin/api/users/${user.user_id}`, 'DELETE');
        onDelete(user.user_id); //    
        onClose(); //  
      } catch (err) {
        console.error("  ", err);
      }
    }
  };

  if (!user) return null;

  return (
    <div className="modal-backdrop" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <h3>  </h3>
        <p><strong>ID:</strong> {user.user_id}</p>
        <p><strong>:</strong> {user.email}</p>
        <p><strong>:</strong> {user.nickname}</p>
        <p><strong>:</strong> {user.created_at?.split('T')[0] || ''}</p>
        <p><strong> :</strong> {user.preferred_factor}</p>

        <div className="rating-list">
          <h4> </h4>
          <ul>
            {ratings.map((r) => (
              <li key={r.rating_id}>
                {r.p_name} - {r.score} ({r.created_at?.split('T')[0]})
              </li>
            ))}
          </ul>
        </div>

        <div className="modal-footer">
          <button className="admin-btn" onClick={onClose}></button>
          <button className="admin-btn primary" onClick={handleViewRatings}>  </button>
          <button className="admin-btn danger" onClick={handleDelete}></button>
        </div>
      </div>
    </div>
  );
}
