import { useEffect, useState, useContext } from "react";
import { apiRequest } from "../api/api";
import { UserContext } from "../context/UserContext";
import { useNavigate } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrashAlt, faPlus, faSearch } from "@fortawesome/free-solid-svg-icons";
import { toast } from "react-toastify";
import "./BookmarkList.css";

const BookmarkList = () => {
  const { user } = useContext(UserContext);
  const [bookmarks, setBookmarks] = useState([]);        //  : [{ p_id, name, address, fee, avg_rating, bookmarkId }, ]
  const [searchResults, setSearchResults] = useState([]); //  : parking-lots API  
  const [searchTerm, setSearchTerm] = useState("");
  const navigate = useNavigate();

  //     
  const fetchBookmarks = async () => {
    try {
      const res = await apiRequest("/api/bookmarks", "GET", null, user?.email);
      setBookmarks(res.data);
    } catch (err) {
      console.error("  ", err);
      toast.error("  .");
    }
  };

  //    user.email    FETCH
  useEffect(() => {
    if (user?.email) fetchBookmarks();
  }, [user?.email]);

  // 
  const handleSearch = async () => {
    if (!searchTerm.trim()) return;
    try {
      const encoded = encodeURIComponent(searchTerm);
      const res = await apiRequest(`/api/parking-lots/search?keyword=${encoded}`);
      setSearchResults(res.data);
    } catch (err) {
      console.error(" ", err);
      toast.error("   .");
    }
  };
  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      handleSearch();
    }
  };

  //  
  const handleDelete = async (bookmarkId) => {
    try {
      await apiRequest(`/api/bookmarks/${bookmarkId}`, "DELETE", null, user?.email);
      toast.success(" .");
      await fetchBookmarks();
    } catch (err) {
      console.error(" ", err);
      toast.error(err.message || " .");
    }
  };

  //  
  const handleAdd = async (lot) => {
    try {
      await apiRequest("/api/bookmarks", "POST", { p_id: lot.p_id }, user?.email);
      toast.success(" .");
      await fetchBookmarks();
    } catch (err) {
      console.error(" ", err);
      toast.error(" .");
    }
  };

  //    p_id 
  const handleClickCard = (lot) => {
    navigate("/home", { state: { targetParkingId: lot.p_id } });
  };

  // 
  const renderCard = (lot, isSearch = false) => (
    <li
      key={lot.bookmarkId ?? lot.p_id}
      className="bookmark-card"
      onClick={() => !isSearch && handleClickCard(lot)}
    >
      <div className="card-header">
        <h3>{lot.name}</h3>
        {isSearch ? (
          <button
            onClick={(e) => {
              e.stopPropagation();
              handleAdd(lot);
            }}
            className="add-btn"
          >
            <FontAwesomeIcon icon={faPlus} />
          </button>
        ) : (
          <button
            onClick={(e) => {
              e.stopPropagation();
              handleDelete(lot.bookmarkId);
            }}
            className="delete-btn"
          >
            <FontAwesomeIcon icon={faTrashAlt} />
          </button>
        )}
      </div>
      <p className="lot-address">{lot.address}</p>
      <div className="lot-info-group">
        <span>
          :{" "}
          {lot.fee != null
            ? `5 ${lot.fee.toLocaleString()}`
            : " "}
        </span>
        <span>
          : {lot.avg_rating != null ? lot.avg_rating.toFixed(1) : "0.0"}
        </span>
      </div>
    </li>
  );

  // bookmarked list is just bookmarks[]
  return (
    <div className="bookmark-list-container">
      <h2> </h2>

      <div className="search-bar">
        <input
          type="text"
          placeholder="   "
          className="search-input"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          onKeyDown={handleKeyPress}
        />
        <button onClick={handleSearch} className="search-button">
          <FontAwesomeIcon icon={faSearch} />
        </button>
      </div>

      {searchResults.length > 0 && (
        <ul className="search-results">
          {searchResults
            .filter((lot) => !bookmarks.some((b) => b.p_id === lot.p_id))
            .map((lot) => renderCard(lot, true))}
        </ul>
      )}

      <hr />

      {bookmarks.length === 0 ? (
        <p className="no-bookmarks">  .</p>
      ) : (
        <ul>{bookmarks.map((lot) => renderCard(lot))}</ul>
      )}
    </div>
  );
};

export default BookmarkList;